<template>
	<view @click="closeAllOpt()">
		<kt-nav-bar ref="navBar" title="发布帖子">
			<template v-if="!isPublishing">
				<view v-if="(requestParam.content != '' && requestParam.content != null) || urls.length > 0"
					@click="publish" class="publish-btn publish-btn-has-value">发布</view>
				<view v-else class="publish-btn">发布</view>
			</template>
			<template v-else>
				<view class="publish-btn">发布中</view>
			</template>
		</kt-nav-bar>


		<view v-if="isPublishing"
			style="position: fixed;top: 0;left: 0;right: 0;bottom: 0;background-color: rgba(0,0,0,0.5);z-index: 9999;">
			>
			<view style="position: absolute;top: 50%;left: 50%;transform: translate(-50%, -50%);">
			</view>
		</view>

		<view class="box">
			<view style="padding: 20rpx 20rpx 0 20rpx;">
				<textarea maxlength="500" :style="{
					'max-height': 'calc(100vh - ' + navBarHeight + 'px)'
				}" :auto-height="true" :placeholder-style="'color:#999;font-size:32rpx;'" placeholder="写下你想说的话..."
					class="box-textarea" v-model="requestParam.content">
											</textarea>
			</view>

			<view style="height: 30rpx;"></view>

			<view style="padding: 10rpx 10rpx 0 10rpx;box-sizing: border-box;">
				<view v-for="(item, index) in urls"
					:style="(index == 0 || index == 3 || index == 6 ? 'margin-left: 17rpx' : '')" @longtap="openOpt(index)"
					@blur="closeOpt(index)" tabindex="-1" ref="'imgBtn'" class="img-btn">

					<image :src="item.path" @click="previewImage(index)" mode="aspectFill" class="img-btn-img"></image>

					<!-- 删除按钮 -->
					<view v-if="!item.optShow" class="img-btn-delete" @click="urls.splice(index, 1)">
						<image :src="$kt.file.byPath('image/icon/delete.svg')" class="img-btn-delete-img"></image>
					</view>

					<!-- 遮挡 -->
					<view v-if="item.optShow" class="img-btn-opt">
						<image v-if="index > 0" style="left:10rpx;transform:rotate(180deg)" @click="moveImg(index, 'left')"
							class="img-btn-opt-move" :src="$kt.file.byPath('image/icon/arrowRight2.svg')">
						</image>

						<image v-if="index < urls.length - 1" :style="{
							'right': '10rpx'
						}" @click="moveImg(index, 'right')" class="img-btn-opt-move"
							:src="$kt.file.byPath('image/icon/arrowRight2.svg')">
						</image>

						<image v-if="index > 2" style="top:10rpx;transform:rotate(-90deg)" @click="moveImg(index, 'top')"
							class="img-btn-opt-move" :src="$kt.file.byPath('image/icon/arrowRight2.svg')">
						</image>

						<image v-if="index < urls.length - 3" style="bottom:10rpx;transform:rotate(90deg)"
							@click="moveImg(index, 'bottom')" class="img-btn-opt-move"
							:src="$kt.file.byPath('image/icon/arrowRight2.svg')">
						</image>

						<!-- 右下角 -->
						<image v-if="index < urls.length - 4 && index != 2"
							style="right:20rpx;bottom:20rpx;transform:rotate(45deg)" @click="moveImg(index, 'rightBottom')"
							class="img-btn-opt-move" :src="$kt.file.byPath('image/icon/arrowRight2.svg')">
						</image>

						<!-- 左下角 -->
						<image v-if="index > 0 && index < urls.length - 2 && index != 3 && index != 6"
							style="left:20rpx;bottom:20rpx;transform:rotate(135deg)" @click="moveImg(index, 'leftBottom')"
							class="img-btn-opt-move" :src="$kt.file.byPath('image/icon/arrowRight2.svg')">
						</image>


						<!-- 左上角 -->
						<image v-if="index > 3 && index != 6" style="left:20rpx;top:20rpx;transform:rotate(-135deg)"
							@click="moveImg(index, 'leftTop')" class="img-btn-opt-move"
							:src="$kt.file.byPath('image/icon/arrowRight2.svg')">
						</image>

						<!-- 右上角 -->
						<image v-if="index > 2 && index != 5 && index != 8"
							style="right:20rpx;top:20rpx;transform:rotate(-45deg)" @click="moveImg(index, 'rightTop')"
							class="img-btn-opt-move" :src="$kt.file.byPath('image/icon/arrowRight2.svg')">
						</image>


					</view>
				</view>
				<view v-if="urls.length < 9" @click="addImg" class="img-btn">
					<text class="add-img-btn-text">+</text>
				</view>
			</view>

			<view style="text-align: right;">
				<text style="margin-right: 20rpx;color: #aaa;font-size: 28rpx;">
					{{ requestParam.content.length }}{{ "/" }}500
				</text>
			</view>

			<view style="height: 20rpx;"></view>


			<!-- 显示位置 -->
			<view class="opt">

				<address-lab @click="toAddress()" :addressTitle="addressTitle"></address-lab>

				<eye-lab @click="$refs.visiblePopup.open()" :visibleCode="requestParam.visibleCode"></eye-lab>

			</view>
		</view>

		<view style="padding: 20rpx;">

		</view>

		<view style="height: 40rpx;"></view>

		<view v-if="false" class="item-view-box">

			<view class="item-view">
				<view style="font-size: 30rpx;color:#333;text-align: left;">温馨提示</view>
				<view class="tip">在发布帖子前请仔细阅读
					<text style="color:#000;">《OVO信息发布服务协议》</text>、如有任何问题可联系官方客服，
					点击发布后即视为您已同意前述协议。
				</view>
			</view>
		</view>

		<view style="height: 40rpx;"></view>

		<visiblePopup @select="visiblePopupSelect" ref="visiblePopup"></visiblePopup>

		<selectAddressShowPopup @select="selectAddressShowPopupSelect" ref="selectAddressShowPopup">
		</selectAddressShowPopup>


	</view>
</template>

<script>
import visiblePopup from './inPublishPost/visiblePopup.vue';
import selectAddressShowPopup from './inPublishPost/selectAddressShowPopup.vue';

export default {
	components: {
		visiblePopup,
		selectAddressShowPopup
	},
	data() {
		return {
			navHeight: 44,
			statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
			navBarHeight: 48,
			urls: [
			],
			requestParam: {
				content: '',
				fileIdListOfImage: [],
				// all 代表所有人可见，mutual 代表互相关注可见，follower 代表关注我的人可见，self 代表自己可见
				visibleCode: "all",
				// 位置的查询id，none 代表不显示位置，"province" 代表显示省份，"city" 代表显示城市，"district" 代表显示区县，其它使用得到的地址
				detailIdOfLocation: "",

			},
			isMoving: false,
			addressTitle: '',
			visibleText: "所有人可见",
			isPublishing: false,

		}
	},
	mounted() {
		this.navHeight = 44;
		// #ifdef MP-WEIXIN
		this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
		// #endif
		this.navBarHeight = this.navHeight - (-this.statusBarHeight);

	},
	methods: {
		async publish() {
			this.isPublishing = true;
			uni.showLoading({
				title: '发布中...',
				mask: true
			})

			for (let i = 0; i < this.urls.length; i++) {
				await new Promise((resolve, reject) => {
					this.$kt.request.uploadFile({
						name: 'file',
						filePath: this.urls[i].path,
						formData: {
							"groupCode": "post"
						},
						success: (res) => {
							var json = JSON.parse(res.data);
							this.requestParam.fileIdListOfImage.push(json.data.id);
							uni.hideLoading();
							resolve(res);
						},
						fail: (err) => {
							reject(err);
						}
					});
				});
			}
			this.$kt.request.request({
				api: this.$api.business.ovo.post.publish,
				data: this.requestParam,
				stateSuccess: (res) => {
					var postDataMe = uni.getStorageSync('postDataMe');
					if (postDataMe) {
						postDataMe.unshift(res.data);
						uni.setStorageSync('postDataMe', postDataMe);
					}else{
						uni.setStorageSync('postDataMe', [res.data]);
					}

					uni.$emit("postPublish");
					uni.redirectTo({
						url: '/pages/public/user-info?isMe=1'
					});
				}
			});
		},
		selectAddressShowPopupSelect(res) {
			this.requestParam.detailIdOfLocation = res.detailIdOfLocation;
			this.addressTitle = res.addressTitle;
		},
		visiblePopupSelect(res) {
			this.requestParam.visibleCode = res.code;
			this.visibleText = res.text;
		},
		openSetting() {
			uni.openSetting({
				success: (res) => {
					console.log(res);
					if (res.authSetting["scope.userLocation"]) {

					}
				}
			});
		},
		getLocation() {
			uni.getLocation({
				type: 'gcj02',
				success: (res) => {
					var latitude = res.latitude.toFixed(5);
					var longitude = res.longitude.toFixed(5);

					this.$kt.request.request({
						api: this.$api.business.ovo.userBind.updateLocation,
						data: {
							latitude,
							longitude
						},
						stateSuccess: (res1) => {
							uni.setStorageSync('ovoSelfInfo', res1.data);
							uni.setStorageSync('pageGetLocationFail', "0");
							setTimeout(() => {
								uni.$emit("changeOvoSelfInfo", res1.data);
								this.toSelectAddress();
							}, 10);
						}
					})
				}
			});
		},
		toSelectAddress() {
			this.$refs.selectAddressShowPopup.open();
		},
		toAddress() {

			uni.getSetting({
				success: (res) => {
					console.log(res);
					if (res.authSetting["scope.userLocation"] == null) {
						this.$ovo.getLocation();
					}
					else if (!res.authSetting["scope.userLocation"]) {
						this.openSetting();
					} else {
						this.toSelectAddress();
					}
				}
			});
		},
		addImg() {
			if (this.urls.length >= 9) {
				uni.showToast({
					title: '最多只能上传9张图片',
					icon: 'none'
				})
				return false;
			}
			uni.chooseImage({
				count: 9 - this.urls.length,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: (res) => {
					// tempFilePath可以作为img标签的src属性显示图片
					const tempFilePaths = res.tempFilePaths;
					// this.urls = this.urls.concat(tempFilePaths);
					for (var i = 0; i < tempFilePaths.length; i++) {
						this.urls.push({
							path: tempFilePaths[i],
							optShow: false
						});
					}
				}
			});
		},
		previewImage(index) {

			var urls = [];
			for (var i = 0; i < this.urls.length; i++) {
				urls.push(this.urls[i].path);
			}

			uni.previewImage({
				current: index,
				urls
			});
		},
		openOpt(index) {
			setTimeout(() => {
				for (var i = 0; i < this.urls.length; i++) {
					this.urls[i].optShow = false;
				}
				this.urls[index].optShow = true;
			}, 10);
		},
		closeOpt(index) {
			this.urls[index].optShow = false;
		},
		closeAllOpt() {
			for (var i = 0; i < this.urls.length; i++) {
				this.urls[i].optShow = false;
			}
		},
		moveImg(index, type) {
			if (this.isMoving) {
				return false;
			}
			this.isMoving = true;
			if (type == 'top') {
				if (index == 0) {
					return false;
				}
				var temp = this.urls[index];
				this.urls.splice(index, 1);
				this.urls.splice(index - 3, 0, temp);
				this.openOpt(index - 3);
			}
			else if (type == 'bottom') {
				if (index == this.urls.length - 1) {
					return false;
				}
				var temp = this.urls[index];
				this.urls.splice(index, 1);
				this.urls.splice(index + 3, 0, temp);
				this.openOpt(index + 3);
			}
			else if (type == "rightBottom") {
				if (index == this.urls.length - 4) {
					return false;
				}
				var temp = this.urls[index];
				this.urls.splice(index, 1);
				this.urls.splice(index + 4, 0, temp);
				this.openOpt(index + 4);
			}
			else if (type == "leftBottom") {
				if (index < 1 || index > this.urls.length - 2) {
					return false;
				}
				var temp = this.urls[index];
				this.urls.splice(index, 1);
				this.urls.splice(index + 2, 0, temp);
				this.openOpt(index + 2);
			} else if (type == "rightTop") {
				if (index < 2 || index > this.urls.length - 1) {
					return false;
				}
				var temp = this.urls[index];
				this.urls.splice(index, 1);
				this.urls.splice(index - 2, 0, temp);
				this.openOpt(index - 2);
			} else if (type == "leftTop") {
				if (index < 3) {
					return false;
				}
				var temp = this.urls[index];
				this.urls.splice(index, 1);
				this.urls.splice(index - 4, 0, temp);
				this.openOpt(index - 4);
			}

			else if (type == 'left') {
				if (index == 0) {
					return false;
				}
				var temp = this.urls[index];
				this.urls.splice(index, 1);
				this.urls.splice(index - 1, 0, temp);
				this.openOpt(index - 1);
			} else {
				if (index == this.urls.length - 1) {
					return false;
				}
				var temp = this.urls[index];
				this.urls.splice(index, 1);
				this.urls.splice(index + 1, 0, temp);
				this.openOpt(index + 1);
			}
			setTimeout(() => {
				this.isMoving = false;
			}, 500);
		},
	}
}
</script>

<style lang="scss" scoped>
.item-view-box {
	padding: 0 30rpx 0 30rpx;
	box-sizing: border-box;
}

.item-view {
	width: 100%;
	bottom: 0;
	padding: 20rpx;
	background-color: #f8f8f8;
	box-sizing: border-box;
	border-radius: 30rpx;
}


.tip {
	font-size: 26rpx;
	color: #999;
	// 开头空两格
	text-indent: 2em;
	// 文字间距
	letter-spacing: 1rpx;
	padding: 20rpx;
	box-sizing: border-box;
}

.publish-btn {
	background-color: #ccc;
	color: #fff;
	border-radius: 5px;
	font-size: 30rpx;
	padding: 10rpx 20rpx 10rpx 20rpx;
	border-radius: 10rpx;
	// 文字间距
	letter-spacing: 2rpx;

}

.publish-btn-has-value {
	background-color: #333;
}

.publish-btn-has-value:active {
	transform: scale(0.9);
}

.box {
	padding: 10rpx;
	box-sizing: border-box;
	// 文字间距
	letter-spacing: 2rpx;

	.box-textarea {
		height: 200rpx;
		min-height: 300rpx;
		max-height: calc(50vh + 200rpx);
		width: 100%;
	}



}

.img-btn {
	position: relative;
	display: inline-block;
	margin: 10rpx;
	background-color: rgb(245, 245, 245);
	border-radius: 10px;
	width: 210rpx;
	height: 210rpx;
	// 文字间距
	text-align: center;
	vertical-align: top;

	.img-btn-img {
		width: 210rpx;
		height: 210rpx;
		border-radius: 10px;
	}

	.add-img-btn-text {
		line-height: 210rpx;
		color: #666;
		font-size: 100rpx;
		font-weight: lighter;
	}

	.img-btn-opt {
		position: absolute;
		width: 100%;
		height: 100%;
		padding: 55rpx;
		left: -55rpx;
		top: -55rpx;
		background-color: rgba(0, 0, 0, 0.7);
		z-index: 999;
		border-radius: 30px;
		display: flex;
		justify-content: center;
		align-items: center;
		// 动画，开始时透明度为0，缩放为0.5，结束时透明度为1，缩放为1
		animation: imgOptTrans .3s;

		.img-btn-opt-move {
			position: absolute;
			width: 60rpx;
			height: 60rpx;
			border-radius: 50%;
			// 颜色翻转
			// filter: invert(100%);
			opacity: .7;
		}

		.img-btn-opt-move:active {
			transform: scale(0.9);
		}
	}
}

@keyframes imgOptTrans {
	0% {
		opacity: 0;
		transform: scale(0.8);
	}

	100% {
		opacity: 1;
		transform: scale(1);
	}
}

.img-btn-delete {
	position: absolute;
	top: 5rpx;
	right: 5rpx;
	width: 40rpx;
	height: 40rpx;
	background-color: rgba(0, 0, 0, 0.7);
	color: #fff;
	border-radius: 55%;

	.img-btn-delete-img {
		width: 35rpx;
		height: 35rpx;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

.img-btn-delete:active {
	opacity: .8;
}

.opt {
	// 文字间距
	vertical-align: top;
	padding: 10rpx;
}

.opt-btn {
	margin: 0 20rpx 0 0;
	margin-bottom: 20rpx;

	.opt-icon {
		width: 30rpx;
		height: 30rpx;
		display: inline-block;
		vertical-align: top;
	}

	.opt-text {
		display: inline-block;
		vertical-align: bottom;
		margin-left: 10rpx;
		font-size: 29rpx;
	}

	display: inline-block;
	background-color: rgb(245, 245, 245);
	border-radius: 10px;
	color: #666;
	padding: 10rpx 20rpx 10rpx 20rpx;
	font-size: 29rpx;
}

.opt-btn:active {
	opacity: .8;
}
</style>
